.sidedrawer{
    height: 100vh;
    background: rgb(253, 237, 237);
    box-shadow: 1px 0px 7px rgba(0,0,0,0.3);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 275px;
    z-index: 200;
}
.sidedrawer ul{
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 0;
    /* padding-right: 15px; */
}
.sidedrawer li div{
    color: #521751;
    font-size: 1.2rem;
    cursor: pointer;
}

.sidedrawer li{
    margin: 0.5rem 0;
}

.sidedrawer li div:hover{
    color: #fa923f;
}

@media (min-width: 1072px){
    .sidedrawer{
        display: none;
    }
}
.backdrop{
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.3);
    z-index: 100;
}
